#footer {
  font-family: "nanumSquareNeo";
}

.footerText {
  margin: 0 16px;
  font-size: 12px;
  text-transform: uppercase;
  border-top: 1px solid var(--black);
  border-bottom: 1px solid var(--black);
  padding-top: 2px;
  display: flex;
  justify-content: space-between;
}

.footerInfo {
  margin: 100px 16px;
  text-transform: uppercase;
  display: flex;
  justify-content: space-between;

  @media (max-width: 800px) {
    flex-direction: column;
  }

  .left {
    width: 50%;

    @media (max-width: 800px) {
      width: 100%;
    }

    .title {
      border-top: 2px solid var(--black);
      border-bottom: 1px solid var(--black);
      padding: 20px 0;
      margin-bottom: 20px;
      font-weight: 700;
      background: url(../../img/arrow.svg) no-repeat right 20px;
      transition: all 0.3s;

      &:hover {
        background-color: var(--black400);
        padding-left: 10px;
        border-color: transparent;
      }
    }

    p {
      padding-bottom: 20px;
      font-size: 12px;
    }
  }

  .right {
    width: 40%;
    border-top: 2px solid var(--black);

    @media (max-width: 800px) {
      width: 100%;
    }

    h3 {
      font-size: 10px;
      margin-bottom: 40px;
      padding-top: 10px;
    }

    ul {
      border-top: 1px solid var(--black);

      li {
        border-bottom: 1px solid var(--black);

        a {
          padding: 14px 0;
          display: inline-block;
          font-size: 14px;
        }

        &:hover em {
          opacity: 1;
        }
      }

      em {
        font-size: 12px;
        padding-left: 10px;
        opacity: 0;
        transition: all 0.3s;
      }
    }
  }
}

.footerRight {
  text-align: center;
  text-transform: uppercase;
  font-size: 12px;
  margin-bottom: 20px;
  line-height: 1.6;
}
